@import url(https://fonts.googleapis.com/css2?family=K2D:wght@200&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: "K2D", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::-webkit-scrollbar {
  width: 0.6rem;
}

body::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0.8);
}

body::-webkit-scrollbar-thumb {
  background-image: linear-gradient(340deg, #dbd9d9, #47aedd 35%, #aca6ff 75%);
  border-radius: 6px;
  border: 1px solid rgb(177, 177, 177);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth;
}

.App {
  background: url(/webportfolio/static/media/clouds2.d504b71f.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
}

.overlay {
  background-color: rgba(255, 255, 255, 0.38);
}

@media (max-width: 500px) {
  .overlay {
    background-color: rgba(255, 255, 255, 0.5);
  }
}

.navBar {
  position: fixed;
  display: flex;
  align-items: center;
  height: 18vh;
  width: 100%;
  z-index: 500;
  border-bottom: 1px solid black;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  font-size: 28px;
  transition: all 400ms ease-in-out;
}

.nav-scrolled {
  height: 12vh;
  font-size: 20px;
  background-color: rgba(117, 158, 173, 0.95);
}

.navBar span {
  padding: 0 3vw;
}

.navBar a {
  text-decoration: none !important;
  color: black !important;
}

.navBar a::after {
  content: "";
  display: block;
  width: 0px;
  margin-top: 4px;
  height: 2px;
  background: pink;
  transition: width 0.3s ease 0s;
}

.navBar a:hover::after {
  width: 150%;
}

@media (max-width: 700px) {
  .navBar {
    justify-content: space-evenly;
  }

  .navBar a:hover::after {
    width: 110%;
  }
}

.HomeContainer {
  height: 100vh;
  padding-top: 25vh;
  text-align: center;
}

.HomeContainer h1 {
  font-size: 70px;
  line-height: 75px;
  font-weight: 500;
  margin: 0;
}

.arrowToHome {
  position: fixed;
  z-index: 1000;
  left: 90%;
  top: 85%;
  width: 80px;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 50%;
  transition: 400ms;
}

.arrowToHome:hover {
  transform: scale(1.2);
}

.personalPic {
  margin-top: 30px;
  width: 250px;
  border-radius: 50%;
  box-shadow: 0 10px 20px black;
}

@media screen and (max-width: 960px) {
  .HomeContainer h1 {
    padding-left: 50px;
    padding-right: 0;
    max-width: 100%;
  }

  .arrowToHome {
    left: 85%;
  }
}

@media screen and (max-width: 735px) {
  .HomeContainer h1 {
    padding-left: 0px;
    font-size: 8.9vw;
    line-height: 10vw;
    text-align: left;
  }

  .arrowToHome {
    left: 80%;
  }
}

@media screen and (max-width: 500px) {
  .HomeContainer h1 {
    padding-left: 0px;
    font-size: 11.6vw;
    line-height: 13.5vw;
    text-align: left;
  }

  .arrowToHome {
    left: 78%;
    width: 60px;
  }
}

@media screen and (max-width: 375px) {
  .HomeContainer h1 {
    font-size: 11.5vw;
    line-height: 13vw;
    text-align: left;
  }

  .arrowToHome {
    left: 75%;
    width: 50px;
  }

  .personalPic {
    width: 180px;
  }
}

.fade-in {
  opacity: 0;
  transform: translateY(50px);
  transition: 500ms ease-in;
}

.fade-in.appear {
  opacity: 1;
  transform: translateY(0px);
}

.AboutContainer h1 {
  font-size: 50px;
  text-align: center;
  font-weight: 400;
  margin: 50px 0;
}

.AboutContainer p {
  font-size: 25px;
  font-weight: 300;
  text-align: center;
  padding: 0 5vw;
}

.about {
  text-align: center;
  padding-top: 15vh;
}

.techIcons {
  position: relative;
  height: 60vh;
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
}

.techIcons img {
  width: 100px;
  padding: 1.5vw;
  cursor: pointer;
  -webkit-filter: drop-shadow(5px 7px 3px black);
          filter: drop-shadow(5px 7px 3px black);
  transition: transform 800ms ease-in-out;
}

.open-techIcons {
  transform: translateY(30vh);
}

.techIcons-text {
  position: absolute;
  top: 50%;
  max-width: 500px;
}

.scoalaIT {
  transition: 200ms ease-in;
}

.scoalaIT:hover {
  transform: scale(0.9);
}

.cv-icon {
  width: 80px;
  transition: 200ms ease-in;
}

.cv-icon:hover {
  transform: scale(0.9);
}

.gh-icon {
  width: 8vw;
  transition: 300ms;
}

.gh-icon:hover {
  transform: scale(1.2);
}

/*  CUSTOM PROPERTIES FOR ICONS TEXT */

@media (min-width: 701px) {
  .first {
    left: 12vw;
    -webkit-animation: techIcons-text-fadeL 1s ease-in-out;
            animation: techIcons-text-fadeL 1s ease-in-out;
  }

  .second {
    left: 30vw;
    -webkit-animation: techIcons-text-fadeL 1s ease-in-out;
            animation: techIcons-text-fadeL 1s ease-in-out;
  }

  .third {
    left: 4vw;
    -webkit-animation: techIcons-text-fadeR 1s ease-in-out;
            animation: techIcons-text-fadeR 1s ease-in-out;
  }

  .fourth {
    right: 30vw;
    -webkit-animation: techIcons-text-fadeR 1s ease-in-out;
            animation: techIcons-text-fadeR 1s ease-in-out;
  }

  .fifth {
    right: 12vw;
    -webkit-animation: techIcons-text-fadeR 1s ease-in-out;
            animation: techIcons-text-fadeR 1s ease-in-out;
  }
}

/* =============================================== */

@media (max-width: 1300px) {
  .third {
    left: 0;
    width: 400px;
  }
}

@media (max-width: 1000px) {
  .third {
    left: 0;
    width: 350px;
  }
}

@media (max-width: 920px) {
  .second {
    width: 350px;
  }

  .third {
    left: 0;
    width: 300px;
  }
}

@media (max-width: 820px) {
  .second {
    width: 350px;
  }

  .third {
    left: 0;
    width: 280px;
  }
}

@media (max-width: 700px) {
  .techIcons {
    flex-direction: column;
    align-items: center;
    height: 150vh;
  }

  .techIcons img {
    width: 80px;
  }

  .techIcons-text {
    width: 250px;
    position: relative;
    top: 0;
  }

  .open-techIcons {
    transform: translateY(0);
  }

  .first {
    -webkit-animation: techIcons-text-fadeR 800ms ease-in-out;
            animation: techIcons-text-fadeR 800ms ease-in-out;
  }

  .second {
    -webkit-animation: techIcons-text-fadeR 800ms ease-in-out;
            animation: techIcons-text-fadeR 800ms ease-in-out;
  }

  .third {
    -webkit-animation: techIcons-text-fadeR 800ms ease-in-out;
            animation: techIcons-text-fadeR 800ms ease-in-out;
  }

  .fourth {
    -webkit-animation: techIcons-text-fadeR 800ms ease-in-out;
            animation: techIcons-text-fadeR 800ms ease-in-out;
  }

  .fifth {
    -webkit-animation: techIcons-text-fadeR 800ms ease-in-out;
            animation: techIcons-text-fadeR 800ms ease-in-out;
  }
}

@media (max-width: 380px) {
  .AboutContainer h1 {
    font-size: 30px;
  }

  .AboutContainer p {
    font-size: 18px;
    padding: 0;
  }

  .scoalaIT {
    width: 200px;
  }

  .techIcons {
    height: 110vh;
  }

  .techIcons img {
    width: 60px;
  }

  .techIcons-text {
    width: 250px;
  }
}

@-webkit-keyframes techIcons-text-fadeL {
  from {
    transform: translateX(50px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes techIcons-text-fadeL {
  from {
    transform: translateX(50px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@-webkit-keyframes techIcons-text-fadeR {
  from {
    transform: translateX(-50px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes techIcons-text-fadeR {
  from {
    transform: translateX(-50px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

